

body {
  background-color: #E9EDF0;
  color: #111111;
}

.App {
  text-align: center;
}

@-webkit-keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

@keyframes App-logo-spin {
  from { -webkit-transform: rotate(0deg); transform: rotate(0deg); }
  to { -webkit-transform: rotate(360deg); transform: rotate(360deg); }
}

.board {
  width: 250px;
  height: 250px;
  /*height: 50%;*/
  /*border: 1px solid black;*/
  /*box-sizing: border-box;*/
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -webkit-box-shadow: 5px 5px 5px grey;
          box-shadow: 5px 5px 5px grey;
}
.board > div {
  display: -ms-flexbox;
  display: flex;
  -ms-flex-pack: center;
      justify-content: center;
  -ms-flex-align: center;
      align-items: center;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  border: 1px solid #9E9E9E;
  width: 33.33%;
  height: 33.33%;
  background-color: #FAFEFE;
  font-size: 25px;
  font-weight: 600;
}
.flex {
  display: -ms-flexbox;
  display: flex;
}
.center {
  -ms-flex-pack: center;
      justify-content: center;
}
.btn {
  margin: 10px;
  border-radius: 5px;
  background-color: #AFB5B0; /* Green */
  border: none;
  color: white;
  padding: 10px 20px;
  text-align: center;
  text-decoration: none;
  display: inline-block;
  font-size: 16px;
}



.btn.focused {
  background-color: #4D7E94; /* Green */
}
.modal {
  width: 100%;
  height: 100%;
  position: absolute;
  opacity: 0.9;
  background-color: white;
}



